import React, {Component} from 'react';
import css from '../css/scss/layout/header.scss';

export default class Header extends Component {
    componentDidMount(){
        const {main, about, carte, services, contact, news,gallery} = this.props;
        const arr = [main, about, carte, services, contact, news,gallery];
        let ask = 0;
        for (let i = 0; i < arr.length; i++){
            if(arr[i] === true ){
                ask = i;
            }
        }
        const header = document.getElementsByClassName('Header')[0];
        const listArray = header.children[0].children;
        switch(ask) {
            case 0 :
                header.classList.add('header_main');
                break;

            case 1:
                header.classList.add('header_about');
                listArray[1].classList.add('active');
                break;
            case 2:
                header.classList.add('header_carte');
                listArray[4].classList.add('active');
                break;
            case 3:
                header.classList.add('header_services');
                listArray[5].children[0].classList.add('active');
                break;
            case 4:
                header.classList.add('header_contact');
                listArray[6].classList.add('active');
                break;
            case 5:
                header.classList.add('header_news');
                listArray[2].classList.add('active');
                break;
            case 6:
                header.classList.add('header_gallery');
                listArray[3].classList.add('active');
                break;

            default : console.log('op'); break;
        }
    }
    render() {

        const { main } = this.props;
        const src = main ? './src/arrow_down.svg' : './src/black_arrow_down.svg';
        return (
            <div className="Header">
                <ul >
                    <li><a href="index.html"></a></li>
                    <li><a href="about.html"> </a></li>
                    <li><a href="news.html"></a></li>
                    <li><a href="gallery.html"></a></li>
                    <li><a href="carte.html"></a></li>
                    <li className="dropdown">
                        <span><a href="hotel.html"> </a></span><img src={src} alt="down"/>
                        <div className="dropdown-content dropdown-main">
                            <a href="hotel.html"></a>
                            <a href="sauna.html"></a>
                            <a href="fishing.html"></a>
                            <a href="banquets.html"></a>
                        </div>
                    </li>
                    <li><a href="contact.html"></a></li>
                </ul>
            </div>
        );
    }
}



